
.chart {
  padding: 20px;
  margin: 20px;
  background: #151515;
  display: inline-block;
}
.chart h3 {
  text-transform: uppercase;
  font-size: 1.4em;
  color: #696969;
  margin: 0;
  margin-bottom: 10px;
}
.chart path {
  fill: none;
  stroke: #6cf391;
  stroke-width: 2px;
}
.chart .axis line {
  stroke: #444444;
}
.chart .axis text {
  fill: #999;
  font-family: 'Cousine'
}



